<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .header {
        .input {
            min-width: 150px;
            max-width: 70%;
        }
        .header-col {
            height: 60px;
        }
        .header-col-select {
            width: 80%;
        }
    }

    .input {
        min-width: 150px;
        max-width: 70%;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }

</style>

<template>
    <div class="page">
        <div>
            <el-row class="header">
                <el-col class="filter-label" :span="2">规则编号:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="data.id" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2"> 状态:</el-col>
                <el-col :span="4">
                    <el-select class="filter-match" v-model="data.status" placeholder="请选择">
                        <el-option
                                v-for="item in statusOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col class="filter-label" :span="2">文章标题:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="data.title" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-btn" :span="6">
                    <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                    <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                    <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
                </el-col>
            </el-row>

            <el-row class="filter">

                <el-col class="filter-label" :span="2"> 发布日期:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="data.time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>
            </el-row>
        </div>
        <el-table
                :data="res.data"
                stripe
                style="width: 100%">

            <el-table-column
                    prop="title"
                    label="标题">
            </el-table-column>
            <el-table-column
                    prop="user.username"
                    label="发布人">
            </el-table-column>
            <el-table-column
                    prop="publish_time"
                    label="发布时间">
            </el-table-column>

            <el-table-column
                    prop="status"
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.status===1?'启用':'未启用'}}</span>
                </template>
            </el-table-column>

            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                    <el-button type="text" size="small" @click="del(scope)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :total="res.last_page"
                    @current-change="loadData"
            >
            </el-pagination>
        </div>

    </div>
</template>

<script>
    import api from '../../js/api/information'

    export default {
        name: "Message",
        data() {
            return {
                data: {
                    id: null,
                    status: null,
                    jump_page: null,
                    title: null,
                    start_time: null,
                    end_time: null,
                    time: null,
                },
                publisher:[],
                statusOptions: [{label: '可用', value: 1}, {label: '不可用', value: 0}],
                res: {},
            }
        },

        mounted() {
            this.loadData(1);
            this.loadPublisher();
        },

        methods: {
            ref() {
                this.data = {
                    id: null,
                    status: null,
                    jump_page: null,
                    title: null,
                    start_time: null,
                    end_time: null,
                    time: null,
                };
                this.loadData(1)
            },
            loadPublisher() {
                // api.publisher((res) => {
                //     this.publisher=res.data.data;
                // })
            },
            loadData(page) {
                this.data.jump_page = page;

                if (this.data.time != null && this.data.time.length === 2) {
                    this.data.start_time = this.data.time[0];
                    this.data.end_time = this.data.time[1]
                } else {
                    this.data.start_time = null;
                    this.data.end_time = null;
                }
                api.index(this.data, res => {
                    this.res = res.data.data;
                    console.log(this.res)
                });
            },
            filterOpen() {
                this.data.isOpen = !this.data.isOpen;
            },
            add() {
                this.$router.push({
                    name: "messageAdd",
                });
            },
            handleClick(item) {
                this.$router.push({
                    name: "messageAdd",
                    query: {
                        id: item.id,
                    },
                });
            },

            del(scope) {
                let _this = this;
                this.$confirm('确认删除' + scope.row.title + '?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.delete({id: scope.row.id}, () => {
                        this.$message.success('删除成功');
                        _this.loadData(1);
                    })
                })
            },
        }
    }
</script>

